import styles from './UCM.module.css';
import { ErrorMessage, Field } from 'formik';
import TextError from '../TextError';
import React from 'react';

const SIDetail = props => {
  const {
    names,
    texts,
    indexGroupEl,
    values,
    push,
    remove,
    name,
    placeholders,
  } = props;
  return (
    <>
      <div className={styles.infoblockGroupSIDetails}>
        {names.map((el, index) => {
          return (
            <div className={styles.inputWrapSIDetail}>
              <label htmlFor={`${name}[${indexGroupEl}]`}>
                {texts[index]}
                <ErrorMessage
                  name={`${name}[${indexGroupEl}]`}
                  component={TextError}
                />
              </label>
              <Field
                className={styles.infoblockDivSIDetails}
                type={'text'}
                placeholder={placeholders[index]}
                name={`${name}[${indexGroupEl}]`}
              />
            </div>
          );
        })}
        <div className={styles.btnBlock}>
          <button
            className={styles.btnAddBlock}
            onClick={e => {
              e.preventDefault();
              push('');
            }}
          >
            Add Details
          </button>
          <button
            className={styles.btnDelBlock}
            disabled={values.length < 2 ? true : false}
            onClick={e => {
              e.preventDefault();
              remove(indexGroupEl);
            }}
          >
            Delete Details
          </button>
        </div>
      </div>
    </>
  );
};

export default React.memo(SIDetail);
