import React from 'react';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { TextField, Button } from '@material-ui/core';
import Stack from '@mui/material/Stack';
import { signUp } from '../../redux/actions/user';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';

const validationSchema = yup.object({
  Email: yup
    .string('Enter your email')
    .email('Enter a valid email')
    .required('Email is required'),
  Password: yup
    .string('Enter your password')
    .min(8, 'Password should be of minimum 8 characters length')
    .required('Password is required'),
  Login: yup
    .string('Enter your Login')
    .min(2, 'Login should be of minimum 2 characters length')
    .required('Login is required'),
  CompanyName: yup
    .string('Enter your company')
    .min(2, 'Company should be of minimum 2 characters length')
    .required('Company is required'),
});

export default function Registration(props) {
  const dispatch = useDispatch();
  const loader = useSelector(state => state.loader);
  let history = useHistory();

  const formik = useFormik({
    initialValues: {
      Email: 'testuser@volgadnepr.com',
      Login: 'test',
      Password: '12345',
      CompanyName: 'VolgaDnepr',
    },
    // validationSchema: validationSchema,
    onSubmit: values => {
      let payload = Object.entries(values).filter(el =>
        el[1] ? el[1].trim() : el[1]
      );
      if (payload.length) {
        payload = Object.fromEntries(payload);
        dispatch(signUp(payload, history, closeModal));
      }
    },
  });

  const { closeModal } = props;

  return (
    <div>
      <form onSubmit={formik.handleSubmit}>
        <TextField
          fullWidth
          id="email"
          name="Email"
          label="Email:*"
          variant="standard"
          margin="normal"
          size="normal"
          value={formik.values.Email}
          onChange={formik.handleChange}
          error={formik.touched.Email && Boolean(formik.errors.Email)}
          helperText={formik.touched.Email && formik.errors.Email}
        />
        <TextField
          fullWidth
          id="password"
          name="Password"
          label="Password:*"
          type="password"
          variant="standard"
          margin="normal"
          size="normal"
          value={formik.values.Password}
          onChange={formik.handleChange}
          error={formik.touched.Password && Boolean(formik.errors.Password)}
          helperText={formik.touched.Password && formik.errors.Password}
        />
        <TextField
          fullWidth
          id="company"
          name="CompanyName"
          label="Company name:*"
          type="text"
          variant="standard"
          margin="normal"
          size="normal"
          value={formik.values.CompanyName}
          onChange={formik.handleChange}
          error={
            formik.touched.CompanyName && Boolean(formik.errors.CompanyName)
          }
          helperText={formik.touched.CompanyName && formik.errors.CompanyName}
        />
        <TextField
          fullWidth
          id="name"
          name="Login"
          label="Name:*"
          type="text"
          variant="standard"
          margin="normal"
          size="normal"
          value={formik.values.Login}
          onChange={formik.handleChange}
          error={formik.touched.Login && Boolean(formik.errors.Login)}
          helperText={formik.touched.Login && formik.errors.Login}
        />
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
          spacing={12}
        >
          <Button size="medium" variant="contained" fullWidth type="submit">
            CREATE ACCOUNT
          </Button>
          <Button
            size="medium"
            variant="contained"
            fullWidth
            onClick={closeModal}
          >
            RETURN TO LOGIN
          </Button>
        </Stack>
      </form>
    </div>
  );
}
