import styles from './UCM.module.css';
import { Formik, Form, FieldArray, Field } from 'formik';
import * as yup from 'yup';
import React from 'react';
import { useState } from 'react';
import InputBlock from '../InputBlock/InputBlock';
import InputBlockSelect from '../InputBlock/InputBlockSelect';
import ElementofIncomingULD from './ElementofIncomingULD';
import ElementofOutgoingULD from './ElementofOutgoingULD';
import SIDetail from './SIDetail';
import Modale from '../Modal/Modal';
import ValidateMessageBlockUCM from '../ValidateMessagesBlock/ValidateMessageBlockUCM';
import { v4 as uuid } from 'uuid';
import { validationSchemaForUCM } from '..//../assets/validationSchemes';
import { arrayForValidationUCM } from '../../assets/arrayForValidationUCM';
import { dayInit } from '../../assets/initValueForForm';
import { MsgService } from '../../services/MsgService';
import { createValidationMessageUCM } from './helperFunctions';

export default function UCM() {
  const [modaleText, setModaleText] = React.useState('');
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [validate, setValidate] = useState(false);
  const [CurValidatioMessage, setCurValidatioMessage] = useState('');
  const [arrayOfGroup, setArrayOfGroup] = useState([uuid()]);

  function openModal() {
    setIsOpen(true);
  }

  const onSubmitUCM = async (values, { setSubmitting, resetForm }) => {
    // try {
    const response = await MsgService.UCMmessage(values);
    if (response.data.status === '200 OK (Message sending successfully)') {
      resetForm();
      setModaleText(
        `Message was sent successfully. Message ID = ${response.data.msggid}`
      );
      openModal();
      setSubmitting(false);
    } else {
      setModaleText(response.data.errors);
      openModal();
    }
    // } catch (error) {
    //   console.log(error);
    //   alert(' ');
    // }
  };
  const validationsSchema = yup.object().shape(validationSchemaForUCM);

  const init = {
    TransmissionChannel: '',
    DeletedULD: false,
    CarrierCode: '',
    FlightNumberFirst: '',
    FlightNumberSecond: '',
    DayOfMonth: '',
    AircraftRegistration: '',
    AirportCode: '',
    IN: [
      {
        Element: [
          {
            ULDIATACode: '',
            ULDSerialNumber: '',
            ULDOwnerCode: '',
          },
        ],
      },
    ],
    OUT: [
      {
        Element: [
          {
            ULDIATACode: '',
            ULDSerialNumber: '',
            ULDOwnerCode: '',
            AirportCodeofUnloading: '',
            ContentCode: '',
          },
        ],
      },
    ],
    SIDetail: [''],
  };
  const textsArrForGroup1 = ['ULD Type', 'ULD Serial Number', 'ULD Owner Code'];
  const namesArrForGroup1 = ['ULDIATACode', 'ULDSerialNumber', 'ULDOwnerCode'];
  const placeholdersArrForGroup1 = [
    'ULDIATACode',
    'ULDSerialNumber',
    'ULDOwnerCode',
  ];
  const textsArrForGroup1Out = [
    'ULD Type',
    'ULD Serial Number',
    'ULD Owner Code',
    'Airport Code of Unloading',
    'Content Code',
  ];
  const namesArrForGroup1Out = [
    'ULDIATACode',
    'ULDSerialNumber',
    'ULDOwnerCode',
    'AirportCodeofUnloading',
    'ContentCode',
  ];
  const placeholdersArrForGroup1Out = [
    'ULDIATACode',
    'ULDSerialNumber',
    'ULDOwnerCode',
    'AirportCodeofUnloading',
    'ContentCode',
  ];
  const textsArrForSIDetail = ['SI Details'];
  const namesArrForSIDetail = ['SIDetail'];
  const placeholdersArrForSIDetail = ['SI Details'];

  return (
    <>
      <Modale
        text={modaleText}
        modalIsOpen={modalIsOpen}
        setIsOpen={setIsOpen}
      />

      <Formik
        enableReinitialize={true}
        initialValues={init}
        validateOnBlur
        validationSchema={validationsSchema}
        onSubmit={onSubmitUCM}
      >
        {props => {
          const { values, errors, touched, isValid, dirty, push } = props;
          return (
            <Form className={styles.form}>
              <div className={styles.fsublock}>
                <h4 className={styles.fsuh4}>Type of transmission channel</h4>
                <InputBlockSelect
                  name="TransmissionChannel"
                  styles={styles}
                  className={styles.infoblockDiv}
                  arrOfValue={[
                    'TransmissionChannel',
                    'Email',
                    'SITA',
                    'Edifly',
                  ]}
                />
              </div>
              <div className={styles.formDiv}>
                {/* Top block  ---------------------------------------------------------  */}
                <h4>Flight Identification</h4>
                <div className={styles.infoblock}>
                  <InputBlock
                    name="CarrierCode"
                    text="Carrier Code"
                    placeholder="CarrierCode"
                    classname="inputWrap"
                  />
                  <InputBlock
                    name="FlightNumberFirst"
                    text="Flight Number(1st)"
                    placeholder="1stFlightNumber"
                    classname="inputWrap"
                  />
                  <InputBlock
                    name="FlightNumberSecond"
                    text="Flight Number(2nd)"
                    placeholder="2ndFlightNumber"
                    classname="inputWrap"
                  />
                  <InputBlockSelect
                    name="DayOfMonth"
                    text="Day Of Month"
                    className={styles.infoblockDiv}
                    arrOfValue={['Day Of Month', ...dayInit]}
                    classname="inputWrap"
                  />
                  <InputBlock
                    name="AircraftRegistration"
                    text="Aircraft Registration"
                    placeholder="AircraftRegistration"
                    classname="inputWrap"
                  />
                  <InputBlock
                    name="AirportCode"
                    text="Airport code (of movement)"
                    placeholder="AirportCodeofMovement"
                    classname="inputWrap"
                  />
                </div>
                <div className={styles.sendingInvDiv}>
                  <label className={styles.sendingInv}>
                    <Field
                      type="checkbox"
                      name="DeletedULD"
                      onClick={() => {
                        if (values.DeletedULD === false) {
                          props.setValues({
                            TransmissionChannel: values.TransmissionChannel,
                            DeletedULD: 'true',
                            CarrierCode: values.CarrierCode,
                            FlightNumberFirst: values.FlightNumberFirst,
                            FlightNumberSecond: values.FlightNumberSecond,
                            DayOfMonth: values.DayOfMonth,
                            AircraftRegistration: values.AircraftRegistration,
                            AirportCode: values.AirportCode,
                          });
                        } else {
                          props.setValues({
                            TransmissionChannel: values.TransmissionChannel,
                            DeletedULD: 'false',
                            CarrierCode: values.CarrierCode,
                            FlightNumberFirst: values.FlightNumberFirst,
                            FlightNumberSecond: values.FlightNumberSecond,
                            DayOfMonth: values.DayOfMonth,
                            AircraftRegistration: values.AircraftRegistration,
                            AirportCode: values.AirportCode,
                            IN: [
                              {
                                Element: [
                                  {
                                    ULDIATACode: '',
                                    ULDSerialNumber: '',
                                    ULDOwnerCode: '',
                                  },
                                ],
                              },
                            ],
                            OUT: [
                              {
                                Element: [
                                  {
                                    ULDIATACode: '',
                                    ULDSerialNumber: '',
                                    ULDOwnerCode: '',
                                    AirportCodeofUnloading: '',
                                    ContentCode: '',
                                  },
                                ],
                              },
                            ],
                            SIDetail: [''],
                          });
                        }
                      }}
                    />
                    Sending removed from station's inventory
                  </label>
                </div>
                {!values.DeletedULD ? (
                  <>
                    {/* Block Incoming ULD  ---------------------------------------------------------  */}
                    <h4>Incoming ULD</h4>
                    <ElementofIncomingULD
                      values={values}
                      placeholdersArrForGroup1={placeholdersArrForGroup1}
                      namesArrForGroup1={namesArrForGroup1}
                      arrayOfGroup={arrayOfGroup}
                      namenamesArrForGroup1s={namesArrForGroup1}
                      textsArrForGroup1={textsArrForGroup1}
                      setArrayOfGroup={setArrayOfGroup}
                    />
                    <h4>Outgoung ULD</h4>
                    <ElementofOutgoingULD
                      values={values}
                      placeholdersArrForGroup1={placeholdersArrForGroup1Out}
                      namesArrForGroup1={namesArrForGroup1Out}
                      arrayOfGroup={arrayOfGroup}
                      textsArrForGroup1={textsArrForGroup1Out}
                      setArrayOfGroup={setArrayOfGroup}
                    />
                    <h4>Supplementary Information</h4>
                    <FieldArray name="SIDetail">
                      {({ push, remove }) => {
                        return (
                          <>
                            <div className={styles.infoblock}>
                              {values.SIDetail.map((el, indexSIDetail) => {
                                return (
                                  <div className={styles.divElement}>
                                    <p
                                      className={styles.elementTitle}
                                    >{`SI Detail${indexSIDetail + 1}`}</p>

                                    <SIDetail
                                      name="SIDetail"
                                      values={values.SIDetail}
                                      indexElement={indexSIDetail}
                                      push={push}
                                      remove={remove}
                                      key={indexSIDetail}
                                      placeholders={placeholdersArrForSIDetail}
                                      names={namesArrForSIDetail}
                                      texts={textsArrForSIDetail}
                                      setArrayOfG={setArrayOfGroup}
                                      indexGroupEl={indexSIDetail}
                                    />
                                  </div>
                                );
                              })}
                            </div>
                          </>
                        );
                      }}
                    </FieldArray>
                  </>
                ) : null}
                {/* <div className={styles.btnBlock}> </div>
                <pre>{`${JSON.stringify(values, null, 2)}`}</pre> */}
                {/* Block Validate message  ---------------------------------------------------------  */}
                <ValidateMessageBlockUCM
                  validate={validate}
                  values={values}
                  CurValidatioMessage={CurValidatioMessage}
                  createValidationMessageUCM={createValidationMessageUCM}
                  setCurValidatioMessage={setCurValidatioMessage}
                  setValidate={setValidate}
                  arrayForValidationUCM={arrayForValidationUCM}
                />
                <div className={styles.publish}>
                  <button
                    className={styles.message}
                    disabled={!isValid && !dirty}
                  >
                    Publish
                  </button>
                </div>
              </div>
            </Form>
          );
        }}
      </Formik>
    </>
  );
}
