import InputBlock from '../InputBlock/InputBlock';
import styles from './UCM.module.css';
import React from 'react';

const GroupEl = props => {
  const {
    names,
    texts,
    indexGroupEl,
    values,
    push,
    remove,
    name,
    placeholders,
  } = props;
  return (
    <>
      <h5 className={styles.groupTitle}>Group {`${indexGroupEl + 1}`}</h5>
      <div className={styles.infoblockGroup}>
        {names.map((el, index) => {
          return (
            <InputBlock
              name={`${name}[${indexGroupEl}].${names[index]}`}
              text={texts[index]}
              placeholder={placeholders[index]}
              classname="inputWrap"
            />
          );
        })}
        <div className={styles.btnBlock}>
          {/* {console.log(values)} */}

          <button
            className={styles.btnAddBlock}
            disabled={values.length > 4 ? true : false}
            onClick={e => {
              e.preventDefault();
              push({
                ULDIATACode: '',
                ULDSerialNumber: '',
                ULDOwnerCode: '',
              });
            }}
          >
            Add Group
          </button>
          <button
            className={styles.btnDelBlock}
            disabled={values.length < 2 ? true : false}
            onClick={e => {
              e.preventDefault();
              remove(indexGroupEl);
            }}
          >
            Delete group
          </button>
        </div>
      </div>
    </>
  );
};

export default React.memo(GroupEl);
