import { FieldArray } from 'formik';
import GroupEl from './GroupEl';
import styles from './UCM.module.css';
import React from 'react';

const ElementofIncomingULD = ({
  values,
  arrayOfGroup,
  namesArrForGroup1,
  textsArrForGroup1,
  setArrayOfGroup,
  placeholdersArrForGroup1,
}) => {
  return (
    <>
      <FieldArray name="IN">
        {({ push, remove }) => {
          return (
            <>
              {values.IN.map((el, indexElement) => {
                return (
                  <div className={styles.infoblock}>
                    <div className={styles.btnBlockElement}>
                      <button
                        className={styles.btnAddElement}
                        onClick={e => {
                          e.preventDefault();
                          push({
                            Element: [
                              {
                                ULDIATACode: '',
                                ULDSerialNumber: '',
                                ULDOwnerCode: '',
                              },
                            ],
                          });
                        }}
                      >
                        Add Element
                      </button>
                      <button
                        className={styles.btnDelElement}
                        disabled={values.IN.length < 2 ? true : false}
                        onClick={e => {
                          e.preventDefault();
                          remove(indexElement);
                        }}
                      >
                        Delete Element
                      </button>
                    </div>

                    <FieldArray name={`IN[${indexElement}].Element`}>
                      {({ push, remove, replace }) => {
                        return (
                          <div className={styles.divElement}>
                            <p className={styles.elementTitle}>{`Element${
                              indexElement + 1
                            }`}</p>
                            {values.IN[indexElement].Element.map(
                              (el, indexGroupEl) => {
                                return (
                                  <GroupEl
                                    name={`IN[${indexElement}].Element`}
                                    values={values.IN[indexElement].Element}
                                    indexElement={indexElement}
                                    push={push}
                                    remove={remove}
                                    replace={replace}
                                    key={el}
                                    id={el}
                                    placeholders={placeholdersArrForGroup1}
                                    arrayOfGroup={arrayOfGroup}
                                    names={namesArrForGroup1}
                                    texts={textsArrForGroup1}
                                    setArrayOfG={setArrayOfGroup}
                                    indexGroupEl={indexGroupEl}
                                  />
                                );
                              }
                            )}
                          </div>
                        );
                      }}
                    </FieldArray>
                  </div>
                );
              })}
            </>
          );
        }}
      </FieldArray>
    </>
  );
};

export default React.memo(ElementofIncomingULD);
