import ValidateExampleUCM from './ValidateExampleUCM';
import styles from './ValidateMessagesBlock.module.css';

export default function ValidateMessageBlockUCM(props) {
  const {
    validate,
    CurValidatioMessage,
    createValidationMessageUCM,
    values,
    arrayForValidationUCM,
    setCurValidatioMessage,
    selectFSUValue,
    setValidate,
  } = props;
  return (
    <div className={styles.validateMessage}>
      {validate === false ? (
        <ValidateExampleUCM />
      ) : (
        <div className={styles.validate}>{CurValidatioMessage}</div>
      )}
      {validate === false ? (
        <button
          className={styles.message}
          onClick={e => {
            e.preventDefault();
            createValidationMessageUCM(
              values,
              arrayForValidationUCM,
              setCurValidatioMessage,
              selectFSUValue
            );
            setValidate(true);
          }}
        >
          Validate message
        </button>
      ) : (
        <button
          className={styles.message}
          onClick={e => {
            e.preventDefault();
            setValidate(false);
          }}
        >
          Show example{' '}
        </button>
      )}
    </div>
  );
}
