import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useDispatch } from 'react-redux';
import { useHistory, useLocation } from 'react-router';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import Registration from '../Registration/Registration';
import { useFormik } from 'formik';
import * as yup from 'yup';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { Container } from '@material-ui/core';
import { signIn } from '../../redux/actions/user';

const validationSchema = yup.object({
  Login: yup.string('Enter your Login').required('Login is required'),
  Password: yup
    .string('Enter your password')
    .min(4, 'Password should be of minimum 8 characters length')
    .required('Password is required'),
});

const useStyles = makeStyles(theme => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '25ch',
    },
  },
  head: {
    fontSize: '50px',
    textAlign: 'center',
  },
  but: {
    color: 'white',
    background: 'blue',
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    borderRadius: '10px',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    maxWidth: '500px',
  },
}));

export default function Auth() {
  const classes = useStyles();
  let history = useHistory();
  let location = useLocation();

  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  let { from } = location.state || { from: { pathname: '/' } };

  const dispatch = useDispatch();

  const formik = useFormik({
    initialValues: {
      Login: '',
      Password: '',
    },
    validationSchema: validationSchema,
    onSubmit: values => {
      let payload = Object.entries(values).filter(el =>
        el[1] ? el[1].trim() : el[1]
      );
      if (payload.length) {
        payload = Object.fromEntries(payload);
        dispatch(signIn(payload, history, from));
      }
    },
  });

  return (
    <Container maxWidth="xs">
      <div>
        <h1 className={classes.head}>UI Agent</h1>
        <form onSubmit={formik.handleSubmit}>
          <TextField
            fullWidth
            className={classes.root}
            id="login"
            name="Login"
            label="Login"
            variant="standard"
            margin="normal"
            size="normal"
            value={formik.values.Login}
            onChange={formik.handleChange}
            error={formik.touched.Login && Boolean(formik.errors.Login)}
            helperText={formik.touched.Login && formik.errors.Login}
          />

          <TextField
            fullWidth
            size="normal"
            id="password"
            name="Password"
            label="Password"
            variant="standard"
            type="password"
            margin="normal"
            value={formik.values.Password}
            onChange={formik.handleChange}
            error={formik.touched.Password && Boolean(formik.errors.Password)}
            helperText={formik.touched.Password && formik.errors.Password}
          />

          <Button variant="contained" fullWidth type="submit">
            Submit
          </Button>
        </form>
        <br />
      </div>
      <div>
        <Button variant="text" onClick={handleOpen}>
          Create Account
        </Button>

        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={open}
          onClose={handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >
          <Fade in={open}>
            <div className={classes.paper}>
              <h2 id="transition-modal-title">Registration</h2>
              <Registration closeModal={handleClose} />
            </div>
          </Fade>
        </Modal>
      </div>
    </Container>
  );
}
