import MyButton from '../MyButton/MyButton';
import logo from '../../components/img/logo_VD-en.jpg';
import { Link, useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';
import Stack from '@mui/material/Stack';
import { useDispatch } from 'react-redux';
import { signOut } from '../../redux/actions/user';

export default function Header() {
  const isAuth = useSelector(store => store.user.isAuth);
  const history = useHistory();
  const dispatch = useDispatch();

  function toFFM() {
    history.push('/messages');
  }
  function toSignIn() {
    history.push('/');
  }
  return (
    <header className="header">
      <Link to="/">
        <img className="logo" src={logo} alt="logoVolga-Dnepr" />
      </Link>
      <nav className="navigation">
        <Stack
          justifyContent="center"
          alignItems="center"
          direction={{ xs: 'column', sm: 'row' }}
          spacing={2}
        >
          {isAuth ? (
            <>
              <MyButton onClick={toFFM}>Messages</MyButton>
              <MyButton onClick={() => dispatch(signOut(history))}>
                Logout
              </MyButton>
            </>
          ) : (
            <>
              <MyButton onClick={toSignIn}>Sign In</MyButton>
            </>
          )}
        </Stack>
      </nav>
    </header>
  );
}
