import { TrafficRounded } from '@material-ui/icons';
import axios from 'axios';
import { API_URL } from '../../http';
import { AuthService } from '../../services/AuthService';
import { DELETE_USER, SET_USER, SET_AUTH, UNAUTH } from '../types/types';
import { enableLoader, disableLoader } from './loader';

export const setUser = user => ({
  type: SET_USER,
  payload: user,
});

export const unAuth = () => ({
  type: UNAUTH,
});

export const signUp = (payload, history, closeModal) => async dispatch => {
  // console.log('stringifyyyyyyyyyyyyy', JSON.stringify(payload));
  dispatch(enableLoader());
  try {
    const response = await AuthService.registration(payload);
    console.log('response in registration');
    if (response.status === 200 && response.data.status) {
      console.log('    ', response);
      // localStorage.setItem('token', response.data.access_token);
      // dispatch(setAuth());
      alert(`${response.data.status}`);
      closeModal();
    } else {
      alert(`: ${response.data.error}`);
      history.replace('/');
    }
  } catch (e) {
    console.log(e);
    alert(` HTTP: ${e}`);
  }
  dispatch(disableLoader());
};

export const setAuth = () => ({
  type: SET_AUTH,
});

export const signIn = (payload, history, from) => async dispatch => {
  dispatch(enableLoader());
  try {
    const response = await AuthService.login(payload);

    if (response.status === 200 && !response.data.error) {
      console.log('      ', response);
      localStorage.setItem('token', response.data.access_token);
      dispatch(setAuth());
      history.replace('/messages');
    } else if (response.status === 200 && response.data.error) {
      console.log(response);
      alert(`: ${response.data.error}`);
    }

    if (response.status === 401 || response.status === 401) {
      alert('  ,  ');
    }
  } catch (e) {
    console.log(e);
  }

  dispatch(disableLoader());
};

export const signOut = history => async dispatch => {
  try {
    const response = await AuthService.logout();
    console.log('     ', response);
    if (response.status === 200) {
      console.log(response);
      dispatch(unAuth());
      localStorage.removeItem('token');
      history.replace('/');
    }
  } catch (e) {
    alert('    ', e);
  }
};

export const deleteUser = () => ({
  type: DELETE_USER,
});

export const checkAuth = (from, history) => async dispatch => {
  try {
    // const response = await axios.get(`${API_URL}V1/refresh`, {
    //   withCredentials: false,
    // });
    const response = await AuthService.refresh();
    if (response.status === 200) {
      localStorage.setItem('token', response.data.access_token);
      // dispatch(setUser(response));
      dispatch(setAuth());
      history.push(`${from}`);
    }
  } catch (e) {
    console.log(e);
    history.push('/');
  }
};
