import styles from './fsu.module.css';
import { Formik, Form } from 'formik';
import * as yup from 'yup';
import React, { useEffect } from 'react';
import { useState } from 'react';
import { initVal } from '../../assets/initValue';
import { validationSchemaObjAllProps } from '../../assets/validationSchemes';
import InputBlock from '../InputBlock/InputBlock';
import InputBlockSelect from '../InputBlock/InputBlockSelect';
import MyField from '../InputBlock/MyField';
import { objOfFSUSchemes } from '../../assets/objOfFSUSchemes';
import Modale from '../Modal/Modal';
import ValidateMessageBlock from '../ValidateMessagesBlock/ValidateMessagesBlock';
import { MsgService } from '../../services/MsgService';
import {
  monthInit,
  dayInit,
  dayChangeIndicator,
} from '../../assets/initValueForForm';

export default function FSU() {
  const [selectFSUValue, setSelectFSUValue] = useState('StatusCode');
  const [CurValidatioMessage, setCurValidatioMessage] = useState('');
  const [validate, setValidate] = useState(false);
  const [curValifationSchema, setCurValidationSchema] = useState({});
  const [curFSUSchema, setCurFSUSchema] = useState([1, 2]);
  const fsuValues = [
    'ARR',
    'AWD',
    'AWR',
    'BKD',
    'CCD',
    'CRC',
    'DDL',
    'DEP',
    'DIS',
    'DLV',
    'FOH',
    'MAN',
    'NFD',
    'PRE',
    'RCF',
    'RCS',
    'RCT',
    'TFD',
    'TGC',
    'TRM',
  ];
  const [modalIsOpen, setIsOpen] = React.useState(false);
  const [modaleText, setModaleText] = React.useState('');

  function openModal() {
    setIsOpen(true);
  }

  const onSubmit = (values, { setSubmitting, resetForm }) => {
    values.StatusCode = selectFSUValue;
    let count = 0;
    let upperValue = {};

    for (let [key, value] of Object.entries(values)) {
      upperValue[key] = value.toUpperCase();
    }

    if (selectFSUValue !== 'FSU15' && selectFSUValue !== 'StatusCode') {
      (function check() {
        setTimeout(async () => {
          try {
            const response = await MsgService.FSUmessage(
              upperValue,
              selectFSUValue
            );
            if (
              response.data.status === '200 OK (Message sending successfully)'
            ) {
              resetForm();
              setModaleText(
                `Message was sent successfully. Message ID = ${response.data.msggid}`
              );
              openModal();
              setSubmitting(false);
              setSelectFSUValue('FSU15');
            } else {
              setModaleText(response.data.errors);
              openModal();
            }
          } catch (error) {
            if (count === 5) {
              setModaleText(`${error}`);
              openModal();
            } else {
              count++;
              check();
            }
          }
        }, 2000);
      })();
    } else {
      setModaleText('Status code is not selected');
      openModal();
    }
  };
  const curValidationShcemaObj = (FSUcode, initValObj, objOfAllShema) => {
    const resObj = {};
    const curValidationSchemaArr = Object.keys(initValObj[FSUcode]);
    for (let i = 0; i < curValidationSchemaArr.length; i++) {
      if (objOfAllShema[curValidationSchemaArr[i]]) {
        resObj[curValidationSchemaArr[i]] =
          objOfAllShema[curValidationSchemaArr[i]];
      }
      setCurValidationSchema(resObj);
    }
  };

  useEffect(
    () =>
      curValidationShcemaObj(
        selectFSUValue,
        initVal,
        validationSchemaObjAllProps
      ),
    [selectFSUValue]
  );
  useEffect(() => {
    if (selectFSUValue !== 'FSU15' || selectFSUValue !== 'StatusCode') {
      setCurFSUSchema(objOfFSUSchemes[selectFSUValue]);
    }
  }, [selectFSUValue]);

  const validationsSchema = yup.object().shape(curValifationSchema);

  function createValidationMessage(
    objOfValue,
    arrOfValue,
    setCurValidatioMessage,
    curFSU
  ) {
    let resultString = 'FSU/15 \n';

    arrOfValue.forEach((curVal, index) => {
      if (
        objOfValue[curVal] ||
        curVal === 'StatusCode' ||
        curVal === '/' ||
        curVal === '-'
      ) {
        switch (curVal) {
          case 'AirLinePrefix':
            resultString += objOfValue[curVal].toUpperCase();
            break;
          case 'StatusCode':
            resultString += '\n' + curFSU;
            break;
          case 'TransmissionChannel':
          case '/':
            if (
              objOfValue[arrOfValue[index - 1]] ||
              arrOfValue[index - 1] === 'StatusCode'
            ) {
              resultString += curVal;
            }
            break;
          case '-':
            if (objOfValue[arrOfValue[index + 1]]) {
              resultString += curVal;
            }
            break;
          default:
            resultString += objOfValue[curVal].toUpperCase();
        }
      }
    });

    setCurValidatioMessage(resultString);
  }

  const init = initVal[selectFSUValue];

  return (
    <>
      <Modale
        text={modaleText}
        modalIsOpen={modalIsOpen}
        setIsOpen={setIsOpen}
      />
      <Formik
        enableReinitialize={true}
        initialValues={init}
        validateOnBlur
        validationSchema={validationsSchema}
        onSubmit={onSubmit}
      >
        {({ values, errors, touched, isValid, dirty }) => (
          <>
            <Form className={styles.form}>
              <div className={styles.fsublock}>
                <div className={styles.fsu}>
                  <h4 className={styles.fsuh4}>Status code FSU</h4>
                  <select
                    value={selectFSUValue}
                    onChange={e => {
                      setSelectFSUValue(e.target.value);
                    }}
                    name="selectFSU"
                    className={styles.fsublockoption}
                    id="fsu"
                  >
                    <option key="FSU15" value="FSU15">
                      StatusCode
                    </option>
                    {fsuValues.map(el => {
                      return (
                        <option key={el} value={el}>
                          {el}
                        </option>
                      );
                    })}
                  </select>
                </div>
                <h4 className={styles.fsuh4}>Type of transmission channel</h4>
                <InputBlockSelect
                  name="TransmissionChannel"
                  styles={styles}
                  className={styles.fsublockoption}
                  arrOfValue={[
                    'TransmissionChannel',
                    'Email',
                    'SITA',
                    'Edifly',
                  ]}
                />
              </div>
              <div className={styles.formDiv}>
                <h4 className={styles.awb}>AWB Indefication</h4>
                <div className={styles.infoblock}>
                  <InputBlock
                    name="AirLinePrefix"
                    text="AirLine Prefix"
                    placeholder="AirLinePrefix"
                    classname={'inputWrap'}
                  />
                  <InputBlock
                    name="SerialNumber"
                    text="Serial Number"
                    placeholder="SerialNumber"
                    classname={'inputWrap'}
                  />
                  <InputBlock
                    name="AirportCodeofOrigin"
                    text="Airport code of Origin"
                    placeholder="AirportCodeofOrigin"
                    classname={'inputWrap'}
                  />
                  <InputBlock
                    name="AirportCodeofDestination"
                    text="Airport code of Destination"
                    placeholder="AirportCodeofDestination"
                    classname={'inputWrap'}
                  />
                </div>
                <h4>Quantity Detail</h4>
                <div className={styles.infoblockQuantity}>
                  <div className={styles.infoblockQuantityDetail}>
                    <InputBlockSelect
                      className={styles.infoblockDiv}
                      name="ShipmentDescriptionCode"
                      text="Shipment Description Code"
                      arrOfValue={['ShipmentDescriptionCode', 'P', 'T']}
                    />
                    <InputBlock
                      name="NumberofPieces"
                      text="Number of Pieces"
                      placeholder="NumberofPieces"
                      classname={'inputWrap'}
                    />
                    <InputBlockSelect
                      className={styles.infoblockDiv}
                      name="WeightCode"
                      text="Weight Code"
                      arrOfValue={['WeightCode', 'K', 'L']}
                    />
                    <InputBlock
                      name="Weight"
                      text="Weight"
                      placeholder="Weight"
                      classname={'inputWrap'}
                    />
                  </div>

                  <div className={styles.infoblockQuantityDetail}>
                    <h4>Total Consignment Places</h4>
                    <div className={styles.infoblockQuantityDetail}>
                      <MyField
                        type="text"
                        className={styles.infoblockDiv}
                        name="TotalShipmentDescriptionCode"
                        text="Total Shipment Description Code"
                      />
                      <InputBlock
                        disabled={values.ShipmentDescriptionCode !== 'P'}
                        name="TotalNumberofPieces"
                        text="Total Number of Pieces"
                        placeholder="TotalNumberofPieces"
                        classname={'inputWrap'}
                      />
                    </div>
                  </div>
                </div>
                {selectFSUValue === 'CRC' ? (
                  <>
                    <h4>Reporting Detail</h4>
                    <div className={styles.infoblock}>
                      {selectFSUValue === 'CRC' ? (
                        <InputBlockSelect
                          name="DayofReporting"
                          text="Day of Reporting"
                          className={styles.infoblockDiv}
                          arrOfValue={['DayofReporting', ...dayInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'CRC' ? (
                        <InputBlockSelect
                          name="MonthofReporting"
                          text="Month of Reporting"
                          className={styles.infoblockDiv}
                          arrOfValue={['MonthofReporting', ...monthInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'CRC' ? (
                        <InputBlock
                          name="ActualTimeofGivenStatusEvent"
                          text="Actual Time of Given Status Event"
                          placeholder="ActualTimeofGivenStatusEvent"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'CRC' ? (
                        <InputBlock
                          name="AirportCodeofReporting"
                          text="Airport Code of Reporting"
                          placeholder="AirportCodeofReporting"
                          classname={'inputWrap'}
                        />
                      ) : null}
                    </div>
                  </>
                ) : null}
                {selectFSUValue === 'ARR' ||
                selectFSUValue === 'AWR' ||
                selectFSUValue === 'NFD' ||
                selectFSUValue === 'MAN' ||
                selectFSUValue === 'FOH' ||
                selectFSUValue === 'PRE' ||
                selectFSUValue === 'DEP' ||
                selectFSUValue === 'AWD' ||
                selectFSUValue === 'TFD' ||
                selectFSUValue === 'RCS' ||
                selectFSUValue === 'RCF' ||
                selectFSUValue === 'RCT' ||
                selectFSUValue === 'BKD' ||
                selectFSUValue === 'CCD' ||
                selectFSUValue === 'DDL' ||
                selectFSUValue === 'CRC' ||
                selectFSUValue === 'DIS' ||
                selectFSUValue === 'DLV' ||
                selectFSUValue === 'TGC' ||
                selectFSUValue === 'TRM' ? (
                  <>
                    <h4>Movement Detail</h4>
                    <div className={styles.infoblock}>
                      {selectFSUValue === 'CCD' ? (
                        <InputBlockSelect
                          name="DayofClearance"
                          text="Day of Clearance"
                          className={styles.infoblockDiv}
                          arrOfValue={['DayofClearance', ...dayInit]}
                        />
                      ) : null}

                      {selectFSUValue === 'CCD' ? (
                        <InputBlockSelect
                          name="MonthofClearance"
                          text="Month of Clearance"
                          className={styles.infoblockDiv}
                          arrOfValue={['MonthofClearance', ...monthInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'DDL' ? (
                        <InputBlockSelect
                          name="DayofDeliverytoconsigneesdoor"
                          text="Day of Delivery to consignees door"
                          className={styles.infoblockDiv}
                          arrOfValue={[
                            'DayofDeliverytoconsigneesdoor',
                            ...dayInit,
                          ]}
                        />
                      ) : null}
                      {selectFSUValue === 'DDL' ? (
                        <InputBlockSelect
                          name="MonthofDeliverytoconsigneesdoor"
                          text="Month of Delivery to consignees door"
                          className={styles.infoblockDiv}
                          arrOfValue={[
                            'MonthofDeliverytoconsigneesdoor',
                            ...monthInit,
                          ]}
                        />
                      ) : null}
                      {selectFSUValue === 'AWD' || selectFSUValue === 'DLV' ? (
                        <InputBlockSelect
                          name="DayofDelivery"
                          text="Day of Delivery"
                          className={styles.infoblockDiv}
                          arrOfValue={['DayofDelivery', ...dayInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'AWD' || selectFSUValue === 'DLV' ? (
                        <InputBlockSelect
                          name="MonthofDelivery"
                          text="Month of Delivery"
                          className={styles.infoblockDiv}
                          arrOfValue={['MonthofDelivery', ...monthInit]}
                        />
                      ) : null}

                      {selectFSUValue === 'DLV' ? (
                        <InputBlock
                          name="AirportCodeofDelivery"
                          text="Airport Code of Delivery"
                          placeholder="AirportCodeofDelivery"
                          classname={'inputWrap'}
                        />
                      ) : null}

                      {selectFSUValue === 'DDL' ? (
                        <InputBlock
                          name="AirportCodeofDeliverytoconsigneesdoor"
                          text="Airport Code of Delivery to consignees door"
                          placeholder="AirportCodeofDeliverytoconsigneesdoor"
                          classname={'inputWrap'}
                        />
                      ) : null}

                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'RCT' ||
                      selectFSUValue === 'RCF' ||
                      selectFSUValue === 'TFD' ||
                      selectFSUValue === 'TRM' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'CRC' ||
                      selectFSUValue === 'DIS' ? (
                        <InputBlock
                          name="CarrierCode"
                          text="Carrier Code"
                          placeholder="CarrierCode"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'TGC' ||
                      selectFSUValue === 'RCT' ||
                      selectFSUValue === 'TFD' ? (
                        <InputBlockSelect
                          name="DayofTransfer"
                          text="Day of Transfer"
                          className={styles.infoblockDiv}
                          arrOfValue={['DayofTransfer', ...dayInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'RCS' || selectFSUValue === 'FOH' ? (
                        <InputBlockSelect
                          name="DayofReceipt"
                          text="Day of Receipt"
                          className={styles.infoblockDiv}
                          arrOfValue={['DayofReceipt', ...dayInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'NFD' ? (
                        <InputBlockSelect
                          name="DayofNotification"
                          text="Day of Nofication"
                          className={styles.infoblockDiv}
                          arrOfValue={['DayofNotification', ...dayInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'TGC' ||
                      selectFSUValue === 'RCT' ||
                      selectFSUValue === 'TFD' ? (
                        <InputBlockSelect
                          name="MonthofTransfer"
                          text="Month of Transfer"
                          className={styles.infoblockDiv}
                          arrOfValue={['MonthofTransfer', ...monthInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'RCS' || selectFSUValue === 'FOH' ? (
                        <InputBlockSelect
                          name="MonthofReceipt"
                          text="Month of Receipt"
                          className={styles.infoblockDiv}
                          arrOfValue={['DayofReceipt', ...monthInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'NFD' ? (
                        <InputBlockSelect
                          name="MonthofNotification"
                          text="Month of Notification"
                          className={styles.infoblockDiv}
                          arrOfValue={['MonthofNotification', ...monthInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'AWD' ||
                      selectFSUValue === 'FOH' ||
                      selectFSUValue === 'CCD' ||
                      selectFSUValue === 'NFD' ||
                      selectFSUValue === 'RCS' ||
                      selectFSUValue === 'RCT' ||
                      selectFSUValue === 'DDL' ||
                      selectFSUValue === 'DLV' ||
                      selectFSUValue === 'TGC' ||
                      selectFSUValue === 'TFD' ? (
                        <InputBlock
                          name="ActualTimeofGivenStatusEvent"
                          text="Actual Time of Given Status Event"
                          placeholder="ActualTimeofGivenStatusEvent"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'CCD' ? (
                        <InputBlock
                          name="AirportCodeofClearance"
                          text="Airport Code of Clearance"
                          placeholder="AirportCodeofClearance"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'TRM' ||
                      selectFSUValue === 'RCT' ||
                      selectFSUValue === 'TFD' ||
                      selectFSUValue === 'TGC' ? (
                        <InputBlock
                          name="AirportCodeofTransfer"
                          text="Airport Code of Transfer"
                          placeholder="AirportCodeofTransfer"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'RCS' || selectFSUValue === 'FOH' ? (
                        <InputBlock
                          name="MovementAirportCodeofReceipt"
                          text="Airport Code of Receipt"
                          placeholder="MovementAirportCodeofReceipt"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'RCF' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'CRC' ||
                      selectFSUValue === 'DIS' ? (
                        <InputBlock
                          name="FlightNumber"
                          text="Flight Number"
                          placeholder="FlightNumber"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'DIS' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          text="Day of Discrepancy"
                          name="DayofDiscrepancy"
                          arrOfValue={['MonthofDiscrepancy', ...dayInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'DIS' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          text="Month of Discrepancy"
                          name="MonthofDiscrepancy"
                          arrOfValue={['MonthofDiscrepancy', ...monthInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'RCF' ||
                      selectFSUValue === 'AWR' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          text="Day of Scheduled Arrival"
                          name="DayofScheduledArrival"
                          arrOfValue={['DayofScheduledArrival', ...dayInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'RCF' ||
                      selectFSUValue === 'AWR' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          text="Month of Scheduled Arrival"
                          name="MonthofScheduledArrival"
                          arrOfValue={['MonthofScheduledArrival', ...monthInit]}
                        />
                      ) : null}

                      {selectFSUValue === 'DEP' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'CRC' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          text="Day of Scheduled Departure"
                          name="DayofScheduledDeparture"
                          arrOfValue={['DayofScheduledDeparture', ...dayInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'DEP' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'CRC' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          text="Month of Scheduled Departure"
                          name="MonthofScheduledDeparture"
                          arrOfValue={['MonthofScheduledArrival', ...monthInit]}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'DIS' ||
                      selectFSUValue === 'RCF' ? (
                        <InputBlock
                          name="ActualTimeofGivenStatusEvent"
                          text="Actual Time of Given Status Event"
                          placeholder="ActualTimeofGivenStatusEvent"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'RCF' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          name="DayChangeIndicatorArrival"
                          text="Day Change Indicator Arrival"
                          arrOfValue={[
                            'DayChangeIndicatorArrival',
                            ...dayChangeIndicator,
                          ]}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' || selectFSUValue === 'AWR' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          text="Day Change Indicator Movement"
                          name="DayChangeIndicatorMovement"
                          arrOfValue={[
                            'DayChangeIndicatorMovement',
                            ...dayChangeIndicator,
                          ]}
                        />
                      ) : null}

                      {selectFSUValue === 'DIS' ? (
                        <InputBlock
                          name="AirportCodeofDiscrepancy"
                          text="Airport Code of Discrepancy"
                          placeholder="AirportCodeofDiscrepancy"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'DIS' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          name="DiscrepancyCode"
                          text="Discrepancy Code"
                          arrOfValue={[
                            'DiscrepancyCode',
                            'FDAW',
                            'FDCA',
                            'MSAW',
                            'MSCA',
                            'FDAV',
                            'FDMB',
                            'MSAV',
                            'MSMB',
                            'DFLD',
                            'OFLD',
                            'OVCD',
                            'SSPD',
                          ]}
                        />
                      ) : null}
                      {selectFSUValue === 'AWD' ? (
                        <InputBlock
                          name="AirportCodeofDelivery"
                          text="Airport Code of Delivery"
                          placeholder="AirportCodeofDelivery"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'DEP' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'CRC' ? (
                        <InputBlock
                          name="AirportCodeofDeparture"
                          text="Airport Code of Departure"
                          placeholder="AirportCodeofDeparture"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'NFD' ? (
                        <InputBlock
                          name="AirportCodeofNotification"
                          text="Airport Code of Notification"
                          placeholder="AirportCodeofNotification"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'RCF' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'CRC' ? (
                        <InputBlock
                          name="AirportCodeofArrival"
                          text="Airport Code of Arrival"
                          placeholder="AirportCodeofArrival"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'FOH' ||
                      selectFSUValue === 'NFD' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'AWD' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'CCD' ||
                      selectFSUValue === 'DDL' ||
                      selectFSUValue === 'CRC' ||
                      selectFSUValue === 'DIS' ||
                      selectFSUValue === 'DLV' ||
                      selectFSUValue === 'TGC' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'TRM' ||
                      selectFSUValue === 'TFD' ||
                      selectFSUValue === 'RCF' ||
                      selectFSUValue === 'RCT' ||
                      selectFSUValue === 'RCS' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          name="MovementShipmentDescriptionCode"
                          text="Shipment Description Code"
                          arrOfValue={['ShipmentDescriptionCode', 'T', 'P']}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'FOH' ||
                      selectFSUValue === 'NFD' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'AWD' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'CCD' ||
                      selectFSUValue === 'DDL' ||
                      selectFSUValue === 'CRC' ||
                      selectFSUValue === 'DIS' ||
                      selectFSUValue === 'DLV' ||
                      selectFSUValue === 'TGC' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'TRM' ||
                      selectFSUValue === 'TFD' ||
                      selectFSUValue === 'RCF' ||
                      selectFSUValue === 'RCT' ||
                      selectFSUValue === 'RCS' ? (
                        <InputBlock
                          name="MovementNumberofPieces"
                          text="Number of Pieces"
                          placeholder="MovementNumberofPieces"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'FOH' ||
                      selectFSUValue === 'NFD' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'AWD' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'CCD' ||
                      selectFSUValue === 'DDL' ||
                      selectFSUValue === 'CRC' ||
                      selectFSUValue === 'DIS' ||
                      selectFSUValue === 'DLV' ||
                      selectFSUValue === 'TGC' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'TRM' ||
                      selectFSUValue === 'TFD' ||
                      selectFSUValue === 'RCF' ||
                      selectFSUValue === 'RCT' ||
                      selectFSUValue === 'RCS' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          name="MovementWeightCode"
                          text="Weight Code"
                          arrOfValue={['MovementWeightCode', 'K', 'L']}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'NFD' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'FOH' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'AWD' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'CCD' ||
                      selectFSUValue === 'DDL' ||
                      selectFSUValue === 'CRC' ||
                      selectFSUValue === 'DIS' ||
                      selectFSUValue === 'DLV' ||
                      selectFSUValue === 'TGC' ||
                      selectFSUValue === 'TRM' ||
                      selectFSUValue === 'TFD' ||
                      selectFSUValue === 'RCF' ||
                      selectFSUValue === 'RCT' ||
                      selectFSUValue === 'RCS' ? (
                        <InputBlock
                          name="MovementWeight"
                          text={'Weight'}
                          placeholder="MovementWeight"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'RCS' ? (
                        <InputBlock
                          // blockId="received-from-block"
                          // inputId="received-from-input"
                          // onkeydown="this.style.width = ((this.value.length + 1) * 8) + 'px';"
                          name="ReceivedFromDetail"
                          text="Received From Detail"
                          placeholder="ReceivedFromDetail"
                          classname={'inputWrapReceived'}
                        />
                      ) : null}
                    </div>
                  </>
                ) : null}
                {selectFSUValue === 'ARR' ||
                selectFSUValue === 'DEP' ||
                selectFSUValue === 'MAN' ||
                selectFSUValue === 'AWR' ||
                selectFSUValue === 'PRE' ||
                selectFSUValue === 'BKD' ? (
                  <>
                    <h4>Time of Departure Information</h4>
                    <div className={styles.infoblock}>
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          name="TypeofTimeIndicatorDeparture"
                          text="Type Of Time Indicator Departure"
                          arrOfValue={[
                            'TypeofTimeIndicatorDeparture',
                            'A',
                            'E',
                            'S',
                          ]}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ? (
                        <InputBlock
                          name="TimeDeparture"
                          text={'Time Departuve'}
                          placeholder="TimeDeparture"
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          name="DayChangeIndicatorDeparture"
                          text="Day Change Indicator Departure"
                          arrOfValue={[
                            'DayChangeIndicatorDeparture',
                            ...dayChangeIndicator,
                          ]}
                        />
                      ) : null}
                    </div>
                  </>
                ) : null}
                {selectFSUValue === 'ARR' ||
                selectFSUValue === 'DEP' ||
                selectFSUValue === 'PRE' ||
                selectFSUValue === 'AWR' ||
                selectFSUValue === 'MAN' ||
                selectFSUValue === 'BKD' ? (
                  <>
                    <h4>Time of Arrival Information</h4>

                    <div className={styles.infoblock}>
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          name="TypeofTimeIndicatorArrival"
                          text="Type Of Time Indicator Arrival"
                          arrOfValue={[
                            'TypeOfTimeIndicatorArrival',
                            'A',
                            'E',
                            'S',
                          ]}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ? (
                        <InputBlock
                          name="TimeArrival"
                          text={'Time Arrival'}
                          placeholder="TimeArrival"
                          classname={'inputWrap'}
                        />
                      ) : null}
                      {selectFSUValue === 'ARR' ||
                      selectFSUValue === 'MAN' ||
                      selectFSUValue === 'DEP' ||
                      selectFSUValue === 'PRE' ||
                      selectFSUValue === 'AWR' ||
                      selectFSUValue === 'BKD' ||
                      selectFSUValue === 'RCF' ? (
                        <InputBlockSelect
                          className={styles.infoblockDiv}
                          name="DayChangeIndicatorArrival"
                          text="Day Change Indicator Arrival"
                          arrOfValue={[
                            'DayChangeIndicatorArrival',
                            ...dayChangeIndicator,
                          ]}
                        />
                      ) : null}
                    </div>
                  </>
                ) : null}
                <ValidateMessageBlock
                  validate={validate}
                  values={values}
                  CurValidatioMessage={CurValidatioMessage}
                  createValidationMessage={createValidationMessage}
                  curFSUSchema={curFSUSchema}
                  setCurValidatioMessage={setCurValidatioMessage}
                  selectFSUValue={selectFSUValue}
                  setValidate={setValidate}
                />
                <div className={styles.publish}>
                  <button
                    className={styles.message}
                    disabled={!isValid && !dirty}
                  >
                    Publish
                  </button>
                </div>
              </div>
            </Form>
          </>
        )}
      </Formik>
    </>
  );
}
