import TextError from '../TextError';
import { ErrorMessage, Field } from 'formik';
import styles from '../FSU/fsu.module.css';
import React from 'react';

export default React.memo(function InputBlock(props) {
  const { name, text, disabled, blockId, inputId, placeholder, classname } =
    props;

  return (
    <div className={styles[classname]}>
      <label htmlFor={name}>
        {text}
        <ErrorMessage name={name} component={TextError} />
      </label>
      <Field
        className={styles.infoblockDiv}
        type={'text'}
        placeholder={placeholder}
        name={name}
        disabled={disabled}
      />
    </div>
  );
});
