import { DELETE_USER, SET_USER, SET_AUTH, UNAUTH } from '../types/types';

const userReducer = (state = null, action) => {
  switch (action.type) {
    case SET_USER:
      return { ...state, curUser: action.payload };

    case DELETE_USER:
      return { ...state, curUser: null };
    case SET_AUTH: {
      return { ...state, isAuth: true };
    }
    case UNAUTH: {
      return { ...state, isAuth: false, curUser: null };
    }

    default:
      return state;
  }
};

export default userReducer;
