import Auth from './components/Auth/Auth';
import FSU from './components/FSU/FSU';
import UCM from './components/UCM/UCM';
import MenuMsg from './components/MenuMsg/MenuMsg';
import {
  MESSAGES_FSU_ROUTE,
  MESSAGES_ROUTE,
  LOGIN_ROUTE,
  MESSAGES_UCM_ROUTE,
} from './utils/consts';

export const privateRoutes = [
  {
    path: MESSAGES_ROUTE,
    Component: MenuMsg,
  },
  {
    path: MESSAGES_FSU_ROUTE,
    Component: FSU,
  },
  {
    path: MESSAGES_UCM_ROUTE,
    Component: UCM,
  },
];

export const publicRoutes = [
  {
    path: LOGIN_ROUTE,
    Component: Auth,
  },
];
