const createValidationMessageUCM = (
  objOfValue,
  arrOfValue,
  setCurValidatioMessage
) => {
  let resultString = 'UCM \n';

  arrOfValue.forEach((curVal, index) => {
    if (
      objOfValue[curVal] ||
      curVal === '/' ||
      curVal === '.' ||
      curVal === 'br'
    ) {
      switch (curVal) {
        case 'br':
          resultString += '\n';
          break;
        case '/':
          resultString += curVal.toUpperCase();
          break;
        case '.':
          resultString += curVal.toUpperCase();
          break;
        case 'IN':
          resultString += 'IN\n';
          objOfValue[curVal].forEach(el => {
            if (el.Element.length) {
              el.Element.forEach(elem => {
                for (let i in elem) {
                  if (elem[i]) {
                    resultString += '.' + elem[i].toUpperCase();
                  }
                }
              });
            }
            resultString += '\n';
          });
          break;
        case 'OUT':
          resultString += 'OUT\n';
          objOfValue[curVal].forEach(el => {
            if (el.Element.length) {
              el.Element.forEach(elem => {
                for (let i in elem) {
                  if (i === 'ULDOwnerCode' || i === 'AirportCodeofUnloading') {
                    resultString += '/' + elem[i].toUpperCase();
                  } else if (elem[i] || i !== 'ContentCode') {
                    resultString += '.' + elem[i].toUpperCase();
                  } else {
                    resultString += elem[i].toUpperCase();
                  }
                }
              });
            }
            resultString += '\n';
          });
          break;
        case 'SIDetail':
          objOfValue[curVal].forEach(el => {
            resultString += `SI ${el} \n`;
          });
          break;
        default:
          resultString += objOfValue[curVal].toUpperCase();
      }
    }
  });

  setCurValidatioMessage(resultString);
};

export { createValidationMessageUCM };
