import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import { useHistory } from 'react-router';

import {
  Button,
  Box,
  ButtonGroup,
  Typography,
  Container,
} from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  btn: {
    fontFamily: "'MuseoSansCyrl', serif",
    fontWeight: '700',
    maxWidth: '100%',
    color: 'rgb(72, 72, 72)',
    textDecoration: 'none',
    '&:visited': {
      textDecoration: 'none',
    },
  },
  container: {
    display: 'flex',
    justifyContent: 'center',
    maxWidth: '600px',
    minWidth: '300px',
    height: '100%',
    padding: '20px',
    borderRadius: '10px',
    boxShadow: '0px 0px 3px 0px #333',
  },
  text: {
    fontWeight: 900,
  },
}));

export default function MenuMsg() {
  let history = useHistory();

  const classes = useStyles();

  function toFSU() {
    history.push('/messages/FSU');
  }

  function toUCM() {
    history.push('/messages/UCM');
  }

  function toFFM() {
    history.push('/messages/FFM');
  }

  const buttons = [
    <Button
      className={classes.btn}
      size="large"
      variant="outlined"
      key="FSU"
      onClick={toFSU}
    >
      FSU messages
    </Button>,
    <Button
      className={classes.btn}
      size="large"
      variant="outlined"
      key="FSU"
      onClick={toUCM}
    >
      UCM messages
    </Button>,
    <Button
      className={classes.btn}
      size="large"
      variant="outlined"
      key="FSU"
      onClick={toFFM}
    >
      FFM messages
    </Button>,
  ];

  return (
    <Container className={classes.container}>
      <Box
        fullWidth
        sx={{
          maxWidth: '100%',
          height: '100%',
          display: 'flex',
          justifyContent: 'center',
          flexDirection: 'column',
          '& > *': {
            m: 1,
          },
        }}
      >
        <Typography className={classes.text} variant="h6" component="h1">
          Select type of message to generate and send
        </Typography>
        <ButtonGroup
          sx={{
            maxWidth: '100%',
            minWidth: '300px',
            display: 'flex',
            justifyContent: 'center',
          }}
          orientation="vertical"
          aria-label="vertical contained button group"
          variant="contained"
        >
          {buttons}
        </ButtonGroup>
      </Box>
    </Container>
  );
}
