import React from 'react';
import Modal from 'react-modal';

import './Modal.css';

const customStyles = {
  content: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    height: '200px',
    width: '350px',
    top: '48%',
    left: '48%',
    right: 'auto',
    bottom: 'auto',
    margin: '0 auto',
    transform: 'translate(-50%, -50%)',
  },
};

export default function Modale(props) {
  const { text, modalIsOpen, setIsOpen } = props;
  return (
    <>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={() => setIsOpen(false)}
        style={customStyles}
        contentLabel="Modal"
      >
        <div className="modal-div">
          {text}
          <form>
            <button className="modal-btn" onClick={() => setIsOpen(false)}>
               
            </button>
          </form>
        </div>
      </Modal>
    </>
  );
}
