import $api from '../http/index';

export class AuthService {
  static async registration(registrationData) {
    return await $api.post('/V1/CreateAccount', registrationData);
  }
  static async login(signInData) {
    return await $api.post('/V1/authenticate', signInData);
  }
  static async logout() {
    return await $api.post('/V1/logout');
  }
  static async refresh() {
    return await $api.get('/V1/refresh');
  }
}
