export const validationSchemaForUCM = {
  TransmissionChannel: yup
    .string()
    .trim()
    .matches(
      /^(SITA|Edifly|Email)$/gm,
      'The transmission channel type is not selected'
    )
    .required('The transmission channel type is not selected'),
  CarrierCode: yup
    .string()
    .trim()
    .matches(
      /^(([A-Za-z0-9]{2})|([A-Za-z0-9]{2}[A-Za-z]{1}))$/gm,
      "The 'Carrier Code' field has an incorrect format"
    )
    .required('The Carrier Code is not selected'),
  FlightNumberFirst: yup
    .string()
    .trim()
    .matches(
      /^(([0-9]{1,4})|(([0-9]{1,4})[A-Za-z]{1}))$/gm,
      "The 'FlightNumberFirst' field has an incorrect format"
    )
    .required('The FlightNumberFirst is not selected'),
  FlightNumberSecond: yup
    .string()
    .trim()
    .matches(
      /^(([0-9]{1,4})|(([0-9]{1,4})[A-Za-z]{1}))$/gm,
      "Two 'FlightNumberSecond' field has an incorrect format"
    ),
  DayOfMonth: yup
    .string()
    .trim()
    .matches(/^\d{2}$/gm, "The 'DayOfMonth' field has an incorrect format")
    .required('The DayOfMonth is not selected'),
  AircraftRegistration: yup
    .string()
    .trim()
    .matches(
      /^[A-Za-z0-9]{5,7}$/gm,
      "The 'AircraftRegistration' field has an incorrect format"
    )
    .required('The AircraftRegistration is not selected'),
  AirportCode: yup
    .string()
    .trim()
    .matches(
      /^[A_Za-z]{3}$/gm,
      "The 'AirportCode' field has an incorrect format"
    )
    .required('The AirportCode is not selected'),
  IN: yup
    .array(
      yup.object({
        Element: yup
          .array(
            yup.object({
              ULDIATACode: yup
                .string()
                .trim()
                .matches(
                  /^[a-zA-Z]{1}[a-zA-Z\d]{2}$/gm,
                  'ULDIATACode field has an incorrect format'
                )
                .required('The ULDIATACode is not selected'),
              ULDSerialNumber: yup
                .string()
                .trim()
                .matches(
                  /^[A-Za-z0-9]{1}\d{3,11}$/gm,
                  "The 'ULDSerialNumber' field has an incorrect format"
                )
                .required('The ULDSerialNumber is not selected'),
              ULDOwnerCode: yup
                .string()
                .trim()
                .matches(
                  /^(([A-Za-z0-9]{2})|([A-Za-z0-9]{2}[A-Za-z]{1}))$/gm,
                  "The 'ULDOwnerCode' field has an incorrect format"
                ),
            })
          )
          .min(1)
          .max(5),
      })
    )
    .min(1),
  OUT: yup
    .array(
      yup.object({
        Element: yup
          .array(
            yup.object({
              ULDIATACode: yup
                .string()
                .trim()
                .matches(
                  /^[a-zA-Z]{1}[a-zA-Z\d]{2}$/gm,
                  'ULDIATACode field has an incorrect format'
                )
                .required('The ULDIATACode is not selected'),
              ULDSerialNumber: yup
                .string()
                .trim()
                .matches(
                  /^[A-Za-z0-9]{1}\d{3,11}$/gm,
                  "The 'ULDSerialNumber' field has an incorrect format"
                )
                .required('The ULDSerialNumber is not selected'),
              ULDOwnerCode: yup
                .string()
                .trim()
                .matches(
                  /^(([A-Za-z0-9]{2})|([A-Za-z0-9]{2}[A-Za-z]{1}))$/gm,
                  "The 'ULDOwnerCode' field has an incorrect format"
                ),
              AirportCodeofUnloading: yup
                .string()
                .trim()
                .matches(
                  /^[A_Za-z]{3}$/gm,
                  "The 'AirportCode' field has an incorrect format"
                )
                .required('The AirportCode is not selected'),
              ContentCode: yup
                .string()
                .trim()
                .matches(
                  /^[A_Za-z]{1}$/gm,
                  "The 'ContentCode' field has an incorrect format"
                ),
            })
          )
          .min(1)
          .max(3),
      })
    )
    .min(1),
  SIDetail: yup
    .array(
      yup
        .string()
        .trim()
        .matches(/^.{1,61}$/gm, 'Incorrect format, maximum 61 symbols') //11111111111111
    )
    .min(1),
};
